.cell{display:flex;flex-direction:column;align-items:flex-start;width:100%;color:inherit}.cell.-li{display:grid;grid-template-columns:repeat(2, 1fr);align-items:center;gap:0 1rem;padding:1.5rem 0;border-top:1px solid var(--border-color)}@media screen and (max-width: 768px){.cell.-li{grid-template-columns:100%;gap:.25rem;padding:1rem 0}}.cell.-rw{flex-direction:row;align-items:center;gap:.5rem}.cell.-ac{align-items:center;justify-content:center}.cell.-ac *{text-align:center}.cell.-gs{gap:.25rem}.cell.-gm{gap:2rem}.cell.-mb{margin-bottom:1rem}.cell.-hidn{overflow:hidden}