section{display:flex;flex-direction:column;align-items:center;gap:var(--margin-row);width:100%;padding:var(--margin-row) var(--margin);background:var(--background-color);overflow:hidden}section#gallery{padding-top:calc(var(--margin-row)*2);color:var(--background-color);background:var(--foreground-color)}section#gallery h1{position:absolute;top:-0.25em}section#map{padding-top:calc(var(--margin-row)*2)}section#map h1{position:absolute;top:-0.25em}section#map img{width:100vw}