section#cover{display:flex;flex-direction:column;align-items:center;gap:unset;padding:unset;overflow:visible}section#cover .card{position:absolute;z-index:1;background-color:var(--background-color);will-change:transform}section#cover .card:nth-of-type(1){left:0;width:25%}@media screen and (max-width: 768px){section#cover .card:nth-of-type(1){width:50%}}section#cover .card:nth-of-type(2){top:10rem;right:0;width:50%}@media screen and (max-width: 768px){section#cover .card:nth-of-type(2){top:50%;width:75%}}section#cover .imgs{display:flex;width:100%}@media screen and (max-width: 768px){section#cover .imgs{flex-direction:column}}section#cover .imgs .img{display:flex;align-items:center;justify-content:center;width:50%;padding-top:56.25%;will-change:transform;overflow:hidden}@media screen and (max-width: 768px){section#cover .imgs .img{width:100%;padding-top:112.5%}}section#cover .imgs .img:nth-of-type(2){position:absolute;right:0;width:50%}@media screen and (max-width: 768px){section#cover .imgs .img:nth-of-type(2){position:relative;width:100%}}section#cover .imgs .img img{position:absolute;top:50%;left:50%;width:100%;transform:translate3d(-50%, -50%, 0);-o-object-position:center center;object-position:center center;-o-object-fit:cover;object-fit:cover}section#cover .cell{display:flex;flex-direction:column;align-items:center;gap:var(--margin-row);width:100%;padding:var(--margin-row) var(--margin) calc(var(--margin-row)*2);background:var(--brown-color)}section#cover h1{position:absolute;bottom:-0.25em}