.fab{position:fixed;z-index:999;display:flex;align-items:center;bottom:1rem;left:50%;padding:.5rem;background:rgba(var(--black-rgb), 0.75);border-radius:.75rem;will-change:transform,opacity;transform:translate3d(-50%, 200%, 0);opacity:0;transition:transform .6s ease,opacity .6s ease;-webkit-backdrop-filter:blur(0.25rem);backdrop-filter:blur(0.25rem)}.fab span{padding:.5rem .75rem;color:var(--white-color);border-radius:.25rem;font-size:var(--subtext-size);font-weight:var(--headline-weight);line-height:1;letter-spacing:var(--subtext-spacing)}.fab span:hover{text-decoration:underline;text-underline-offset:.25em}.fab.-visible{transform:translate3d(-50%, 0, 0);opacity:1}