section#loading{position:absolute;display:flex;flex-direction:column;align-items:center;justify-content:center;gap:.25rem;top:0;left:0;width:100%;height:100vh;padding:unset;background:var(--brown-color);overflow:hidden}section#loading div{display:flex;align-items:center;justify-content:center;overflow:hidden}section#loading div p{will-change:transform}