.text{font-size:var(--font-size);font-weight:var(--font-weight);letter-spacing:var(--font-spacing);line-height:var(--font-height)}.text:has(span){display:flex;align-items:center;gap:.5em}.text:has(span).-ln2 span,.text:has(span).-lnb span,.text:has(span).-lng span{height:.875em;width:.875em}.text:has(span).-ln2 span::before,.text:has(span).-lnb span::before,.text:has(span).-lng span::before{position:absolute;top:50%;left:50%;color:var(--background-color);font-size:12px;font-weight:640;line-height:1;transform:translate3d(-50%, -50%, 0)}.text:has(span).-ln2 span{background:var(--green-color)}.text:has(span).-ln2 span::before{content:"2"}.text:has(span).-lnb span{background:var(--yellow-color)}.text:has(span).-lnb span::before{content:"분"}.text:has(span).-lng span{background:var(--blue-color)}.text:has(span).-lng span::before{content:"간"}.text:has(span) span{display:flex;align-items:center;height:.375em;width:.375em;background:var(--foreground-color);border-radius:50%}.text.-headline{font-size:var(--headline-size);font-weight:var(--headline-weight);letter-spacing:var(--headline-spacing);line-height:var(--headline-height)}.text.-subheadline{font-size:var(--subheadline-size);font-weight:var(--subheadline-weight);letter-spacing:var(--subheadline-spacing);line-height:var(--subheadline-height)}.text.-label{font-size:var(--label-size);font-weight:var(--label-weight);letter-spacing:var(--label-spacing);line-height:var(--label-height)}.text.-subtext{font-size:var(--subtext-size);font-weight:var(--subtext-weight);letter-spacing:var(--subtext-spacing);line-height:var(--subtext-height)}.text.-tc{text-align:center}.text.-dm{opacity:.625}.text strong{font-family:LibreCaslon,serif;font-style:italic;font-weight:400}a.text{text-decoration:underline;text-underline-offset:.25em}a.text:hover{text-decoration:unset}